.pagination
  $self: &
  &__list
    @extend .module
    display: flex
    align-items: center
    justify-content: center
    flex-wrap: wrap
    gap: 8px
    font-size: 14px

    li
      &.is-disabled
        display: none

      &.is-active
        #{$self}__pagenav
          cursor: default
          background-color: #E8A33C
          border-color: #E8A33C

      &:first-child,
      &:last-child
        a
          display: flex
          align-items: center
          font-weight: 500
          gap: 13px

      &:first-child
        margin-right: 10px
        .icon
          transform: rotate(180deg)
      &:last-child
        margin-left: 10px

      a
        text-decoration: none
        color: #000

  &__pagenav
    display: flex
    align-items: center
    justify-content: center
    width: 50px
    height: 50px
    border-radius: 50px
    border: 1px solid #DCDCDC
