.social
  display: flex
  align-items: center
  gap: 15px

  &__link
    display: flex
    align-items: center
    justify-content: center
    text-decoration: none
    width: 40px
    height: 40px
    flex: 0 0 40px
    border: 1px solid rgba(255, 255, 255, 0.25)
    border-radius: 50%
    transition: all .4s

    &:hover
      background-color: #1C257B
      border-color: #1C257B
