.share
  $self: &
  &--horizontal
    display: flex
    justify-content: space-between
    padding-bottom: 12px
    border-bottom: 1px solid #E8E8E8
    margin-bottom: 34px
    flex-wrap: wrap
    gap: 20px

  &--left
    display: flex
    gap: 10px
    +mq-up(lg)
      flex-direction: column
      flex: 0 0 85px

  &__item
    flex: 0 0 44px

  &__link
    display: flex
    align-items: center
    justify-content: center
    width: 44px
    height: 44px
    background-color: #F6F6F6
    border-radius: 50%
    transition: all .4s

    &.bookmark, &.fav
      border: 1px solid #DCDCDC
      background-color: #fff
      color: #222222

      &.is-active
        border: none
        background-color: $w-orange
        color: #fff

    &.fav
      .icon
        fill: transparent
        stroke: #FC2E60
        stroke-width: 2px

      &.is-active
        background-color: #FC2E60
        .icon
          fill: #fff
          stroke-width: 0

    &:hover
      background-color: darken(#F6F6F6, 10)

  &__social
    display: flex
    align-items: center
    gap: 10px

    &-text
      font-size: 13px
      line-height: 1
      color: #909090

    &-list
      display: flex
      align-items: center
      gap: 10px

  &__bookmark, &__fav
    font-size: 13px
    color: #575757
    line-height: 1
    display: flex
    align-items: center
    gap: 8px
