.modal
  display: none
  position: fixed
  z-index: 1000
  left: 0
  top: 0
  width: 100%
  height: 100%
  padding: 20px 0
  overflow: auto

  &.is-active
    display: flex
    flex-direction: column
    align-items: center
    justify-content: flex-start
  &__wrapper
    display: flex
    flex-direction: column
    background-color: #fff
    border-radius: 6px
    box-shadow: 0px 4px 55px rgba(0, 0, 0, 0.25)
    padding: 17px 28px 28px
    width: 350px
    margin: auto
    text-align: center
    position: relative
    font-size: 14px
  &__close
    width: 30px
    height: 30px
    position: absolute
    border: 1px solid #DCDCDC
    display: flex
    align-items: center
    justify-content: center
    background-color: #fff
    border-radius: 50%
    top: 10px
    right: 15px
    cursor: pointer
    .icon
      width: 9px
      height: 9px
  &__title
    max-width: 340px
    font-size: 19px
    margin-bottom: 8px
  &__content
    font-size: 14px
    margin-bottom: 20px
    color: #555
    line-height: 1.5

  .row
    display: flex
    gap: 10px
    justify-content: center

  .btn
    height: 48px
    font-size: 13px

  .modal-icon
    order: -1
    margin: 0 auto 16px
    width: 36px
    height: 36px
    flex: 0 0 36px
    color: #56900C
