.newsSwiper
  position: relative
  overflow: hidden
  width: 100%

  &__slide
    height: 314px

  &__navigation
    display: flex
    align-items: center
    justify-content: center
    width: 30px
    height: 30px
    border-radius: 50%
    background: rgba(255, 255, 255, 0.38)
    color: #fff
    position: absolute
    top: 50%
    transform: translateY(-50%)
    z-index: 2
    cursor: pointer

  &__prev
    left: 10px
  &__next
    right: 10px
