.btn
  display: inline-flex
  align-items: center
  justify-content: center
  gap: 10px
  height: 48px
  padding: 0 20px
  border-radius: 8px
  text-decoration: none
  font-family: $font-family-base
  font-weight: 500
  font-size: 13px
  line-height: 15px
  border: none
  cursor: pointer
  transition: all .4s
  white-space: nowrap
  line-height: 1

  &--orange
    background-color: $w-orange
    color: $white
    &:hover
      background-color: $p-orange

  &--black
    background-color: #222
    color: $white
    border: 1px solid rgba(255, 255, 255, 0.17)
    &:hover
      background-color: #3E3E3E

  &--white
    color: #222
    font-size: 13px
    font-weight: 500
    border: 1px solid #F3F3F3
    height: 34px
    &:hover
      border-color: $vibrant
      background-color: $vibrant
      color: #000

    .icon
      flex: 0 0 16px
      fill: #969696

  &--more
    display: flex
    width: 180px
    margin: 0 auto
