.recentComment
  background: #F3F3F3
  border-radius: 6px
  padding: 22px 24px
  position: relative
  overflow: hidden

  &::after
    content: ''
    display: block
    position: absolute
    z-index: 1
    pointer-events: none
    width: 100%
    height: 172px
    background: linear-gradient(360deg, #F3F3F3 10.27%, rgba(243, 243, 243, 0) 72.24%)
    left: 0
    bottom: 0

  &__list
    display: flex
    flex-direction: column
    gap: 18px

  &__title
    display: flex
    align-items: center
    gap: 11px
    margin-bottom: 26px

  &__titleIcon
    flex: 0 0 34px

  &__titleText
    font-size: 18px
    font-weight: 600
    line-height: 1.17

  &__avatar
    width: 30px
    height: 30px
    flex: 0 0 30px
    border-radius: 50%
    overflow: hidden

    img
      width: 100%
      height: 100%
      object-fit: cover

  &__author
    display: flex
    align-items: center
    padding-bottom: 15px
    border-bottom: 1px solid #E6E6E6
    margin-bottom: 10px
    gap: 12px
    position: relative

    &::before
      content: ''
      display: block
      width: 9px
      height: 9px
      border-right: 1px solid #e6e6e6
      border-bottom: 1px solid #e6e6e6
      position: absolute
      background-color: #f3f3f3
      left: 44px
      bottom: -6px
      transform: rotate(45deg)

    .name
      font-size: 13px
      font-weight: 500

    .time
      font-size: 11px
      color: #999999

  &__text
    font-size: 12px
    color: #343434
    line-height: 1.58
    margin-bottom: 6px

  &__link
    font-size: 13px
    line-height: 1.54

    a
      color: $w-orange
