@import '../../../node_modules/modern-css-reset/dist/reset'

body
  font-family: $font-family-base
  font-size: 16px
  color: #222
  min-width: 375px

a
  color: $blue
  text-decoration: underline
  &:hover
    text-decoration: none

.container
  width: 100%
  margin: 0 auto
  padding: 0 15px
  +containers(sm)
  +containers(md)
  +containers(lg)
  +containers(xl)
  +containers(xxl)

.row
  display: flex
  flex-wrap: wrap

.column
  display: flex
  flex-direction: column

.aic
  align-items: center

.ai-start
  align-items: flex-start

.ai-str
  align-items: stretch

.jcsb
  justify-content: space-between

.jcc
  justify-content: center

.flw-nowrap
  flex-wrap: nowrap

.flw-sm-nowrap
  +mq-up(sm)
    flex-wrap: nowrap
.flw-lg-nowrap
  +mq-up(lg)
    flex-wrap: nowrap

.flex-1
  flex: 1

.gap-5
  gap: 5px
.gap-10
  gap: 10px
.gap-15
  gap: 15px
.gap-20
  gap: 20px
.gap-25
  gap: 25px
.gap-30
  gap: 30px

.page-title
  @extend .module__title

.columns
  display: flex
  flex-direction: column
  gap: 20px
  +mq-up(lg)
    flex-direction: row

  &--middle
    min-width: 0
    flex: 1

  .profilepage &
    // flex-direction: column-reverse
    align-items: flex-start
    margin-bottom: 100px
    > *
      width: 100%
    +mq-up(lg)
      flex-direction: row

.two-columns
  display: flex
  flex-direction: column
  gap: 20px
  +mq-up(xl)
    display: grid
    grid-template-columns: repeat(2, 1fr)

.four-columns
  display: grid
  grid-template-columns: 1fr
  gap: 20px
  +mq-up(md)
    grid-template-columns: repeat(2, 1fr)
  +mq-up(xl)
    grid-template-columns: repeat(4, 1fr)

.gray-bg
  padding: 52px 0 60px
  background-color: #F3F3F3
