.footer
    &__top
        background-color: #222
        color: $white

        a
            color: inherit

    &__bottom
        padding: 45px 0
        .row
            gap: 30px
    &__text
        color: #494949
        font-size: 13px
        +mq-up(xl)
            max-width: 30%
    &__links
        font-size: 12px
        line-height: 1.67
    &__write
        display: flex
        flex-direction: column
        font-size: 12px
        gap: 5px
        color: #515151
    &__social
        padding: 15px 0
        display: flex
        align-items: center
        font-size: 15px
        gap: 5px
        flex-direction: column
        margin: 0 auto
        +mq-up(lg)
            flex-direction: row
            gap: 25px
            margin: 0 0 0 auto
