.tgLine
  display: flex
  align-items: center
  justify-content: space-between
  flex-direction: column
  padding: 18px 24px
  background: radial-gradient(109.52% 14480.73% at 22.42% 38.1%, #089EEA 0%, #0986E0 100%)
  border-radius: 6px
  gap: 20px
  color: #fff
  position: relative
  overflow: hidden
  +mq-up(xl)
    flex-direction: row

  &::before
    content: ''
    display: block
    position: absolute
    width: 100%
    height: 100%
    left: 0
    top: 0
    right: 0
    bottom: 0
    transform: translateX(-66%)
    background: url('../images/tgLine/bg.svg') no-repeat right center

  &__header
    display: flex
    align-items: center
    gap: 30px
    position: relative
    z-index: 1

  &__headerIcon
    flex: 0 0 54px

  &__headerText
    font-size: 21px
    font-weight: 500
    line-height: 1
    text-align: center
    +mq-up(lg)
      text-align: left

    .categorypage &
      font-size: 19px

  &__content
    font-size: 15px
    line-height: 1.27
    position: relative
    z-index: 1
    text-align: center
    +mq-up(lg)
      text-align: left

    .categorypage &
      font-size: 14px

  &__button
    min-width: 225px
    height: 40px
    position: relative
    z-index: 1
