.tgStatus
  position: relative
  display: flex
  align-items: center
  color: #fff
  +mq-down(lg)
    display: none
  &__icon
    display: flex
    align-items: center
    justify-content: center
    width: 34px
    height: 34px
    border: 1px solid rgba(255, 255, 255, 0.17)
    border-radius: 50px
    position: relative
    cursor: pointer
  &__indicator
    position: absolute
    width: 7px
    height: 7px
    border-radius: 8px
    background: #56900C
    box-shadow: 0px 0px 8px rgba(255, 132, 6, 0.58)
    top: 0
    right: 0
    &.is-animate
      background: #FF8406
      animation: indicator 1s infinite ease-in-out

@keyframes indicator
  0%
    opacity: 1
  100%
    opacity: 0
