.profile-notify
  background: #FFFFFF
  border: 1px solid #F1F1F1
  border-radius: 6px

  &--empty
    display: flex
    flex-direction: column
    gap: 20px
    align-items: center
    padding: 20px
    text-align: center
    font-size: 15px
    color: #575757

    img
      width: 310px
      height: 234px
      object-fit: contain

  a:not([class])
    color: $p-orange

  &__item
    padding: 20px 28px
    font-size: 13px
    &:not(:first-child)
      border-top: 1px solid #f1f1f1

  &__header
    display: flex
    align-items: center
    gap: 9px
    margin-bottom: 14px

    &::before
      content: ''
      display: block
      width: 6px
      height: 6px
      border-radius: 50%
      background-color: #AFAFAF

      .new &
        background-color: $blue

  &__tag
    font-size: 9px
    font-weight: 500
    display: flex
    align-items: center
    justify-content: center
    height: 17px
    border-radius: 3px
    background-color: #56900C
    order: -1
    color: #fff
    padding: 0 6px
    text-transform: uppercase
    line-height: 1

  &__date
    display: flex
    align-items: center
    color: #7D7D7D
    font-size: 12px

  &__body
    color: #565656
    &:not(:last-child)
      margin-bottom: 8px

    .new &
      color: #222

  &__footer
    display: flex
    align-items: center
    justify-content: space-between

  .btn
    text-transform: uppercase
    font-size: 10px
    height: 30px
    line-height: 1
    .icon
      fill: currentColor
