.search-box
  &__title
    margin-bottom: 0
  &__result
    font-size: 15px
    color: #5B5B5B
    margin-bottom: 19px
  &__form
    display: flex
    align-items: center
    background-color: #F2F2F2
    border-radius: 6px
    margin-bottom: 33px

    [type="search"]
      border: none
      background-color: transparent
      display: flex
      flex: 1
      padding: 0 16px
      outline: none
      font-size: 14px
      height: 48px
      &:placeholder
        color: #5C6162

    [type="submit"]
      border: none
      background-color: transparent
      padding: 0
      margin: 0
      display: flex
      align-items: center
      justify-content: center
      cursor: pointer
      width: 32px
      height: 32px
      flex: 0 0 32px

  &__empty
    display: flex
    flex-direction: column
    align-items: center
    font-size: 16px
    text-align: center
    color: #6B6B6B

    img
      width: 396px
      height: 396px
      max-width: 100%
      object-fit: contain
