.post-info
  display: flex
  align-items: center
  color: #969696
  gap: 19px
  font-size: 12px

  > *
    display: flex
    align-items: center
    gap: 5px

    .icon
      flex: 0 0 auto

  &__comment
    &.hot
      color: $w-orange

  &__bull
    display: block
    align-items: center
    justify-content: center
    border-radius: 6px
    width: 4px
    height: 4px
    flex: 0 0 4px
    background-color: currentColor
