.profile
  $self: &
  padding: 11px
  height: 56px
  border-radius: 8px
  display: flex
  align-items: center
  gap: 27px
  position: relative
  margin: 8px 0
  user-select: none
  z-index: 15

  &__notify, &__ava
    width: 34px
    height: 34px
    flex: 0 0 34px
    border-radius: 50%

  &__notify
    display: flex
    align-items: center
    justify-content: center
    background-color: #323232
    color: #fff
    position: relative
    z-index: 5

    #{$self}.is-active &
      background-color: #fff
      border: 1px solid #DCDCDC
      color: #222

    span
      display: flex
      align-items: center
      justify-content: center
      position: absolute
      width: 15px
      height: 15px
      color: #fff
      background-color: $w-orange
      top: -5px
      right: -4px
      border-radius: 50%
      font-size: 9px
      line-height: 14px

  &__ava
    overflow: hidden
    img
      width: 100%
      height: 100%
      object-fit: cover

  &__user
    display: flex
    align-items: center
    gap: 10px
    color: #797979
    position: relative
    z-index: 5

  &__btn
    display: flex
    align-items: center
    gap: 5px
    font-size: 13px
    font-weight: 500
    cursor: pointer

    .icon
      transform: rotate(-90deg)
      width: 10px
      height: 10px
      flex: 0 0 10px

  &__drop
    display: none
    position: absolute
    background-color: #fff
    border-radius: 8px
    padding-top: 56px
    top: 0
    left: 0
    width: 100%
    z-index: 4
    overflow: hidden
    box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.25)

    #{$self}.is-active &
      display: block

  &__link
    display: flex
    align-items: center
    height: 50px
    padding-left: 19px
    padding-right: 10px
    text-transform: uppercase
    color: #313131
    font-size: 11px
    font-weight: 600
    gap: 16px
    text-decoration: none

    .icon
      width: 18px
      height: 18px
      flex: 0 0 18px
      fill: #ADADAD

    sup
      font-size: 10px
      color: #B4B4B4

    &:hover
      background-color: $blue
      color: #fff

      .icon
        fill: #fff

      sup
        color: #fff
