.simplebar
  &-content
    &::before,
    &::after
      display: none

  &-track
    background-color: #D5D8D9
    border-radius: 2px

    &.simplebar-vertical
      width: 6px

      .simplebar-scrollbar
        &::before
          top: 0
          right: 0
          left: 0
          bottom: 0
          background-color: #0C2126
          opacity: 1
