.newsCard
  display: flex
  flex-direction: column
  background-color: #fff
  border: 1px solid #F1F1F1
  border-radius: 6px
  +mq-up(md)
    gap: 20px
    padding: 25px
    flex-direction: row

  .two-columns &
    gap: 14px
    margin-bottom: 0

  .four-columns &
    flex-direction: column
    padding: 0
    gap: 0

  &__image
    display: block
    width: 100%
    height: 173px
    flex: 0 0 173px

    +mq-up(md)
      width: 295px
      flex: 0 0 256px

    .two-columns &
      height: 141px
      flex: 0 0 141px
      +mq-up(md)
        width: 187px
        flex: 0 0 187px

    .four-columns &
      width: 100%
      height: 173px
      flex: 0 0 173px

    img
      width: 100%
      height: 100%
      object-fit: cover
      border-radius: 6px

  &__content
    display: flex
    flex-direction: column
    padding: 16px 25px 25px
    flex: 1
    +mq-up(md)
      padding: 0
    .four-columns &
      padding: 16px 25px 25px

  &__title
    font-size: 18px
    line-height: 1.44
    margin-bottom: 5px
    color: inherit
    font-weight: 500

    .four-columns &
      font-size: 20px
      line-height: 24px
      margin-bottom: 10px

    > a
      color: currentColor
      text-decoration: none
      &:hover
        text-decoration: underline

  &__info
    display: flex
    align-items: center
    color: #969696
    font-size: 12px
    margin-bottom: 8px

    .four-columns &
      order: -1

  &__date
    margin-right: 12px
    &::after
      display: inline-block
      content: '\2022'
      margin-left: 12px

  &__category
    margin-right: 25px

  &__comment
    display: flex
    align-items: center
    gap: 5px
    color: #B0B0B0

    &.hot
      color: $w-orange
      font-weight: 500

  &__commentIcon
    flex: 0 0 16px

  &__text
    margin-bottom: 8px
    line-height: 1.62
    font-size: 13px

  &__footer
    display: flex
    flex-wrap: wrap
    align-items: center
    gap: 17px
    margin-top: auto

  &__timeRead
    display: flex
    align-items: center
    color: #B0B0B0
    font-size: 12px
    gap: 7px

  &__timeReadIcon
    flex: 0 0 14px
    width: 14px
    height: 14px

  &__button
    font-size: 12px
    font-weight: 400

  &__bookmark
    display: flex
    align-items: center
    color: $w-orange
    font-size: 13px
    text-decoration: none
    gap: 5px
    margin-left: auto
    &-icon
      display: flex
      align-items: center
      justify-content: center
      width: 30px
      height: 30px
      flex: 0 0 30px
      border-radius: 50%
      background-color: $w-orange
      color: #fff
