.cat-list
  $self: &
  padding: 10px 0
  position: relative
  user-select: none
  +mq-up(xxl)
    padding: 0

  &__list
    display: none
    +mq-down(xxl)
      background-color: #fff
      position: absolute
      left: 0
      top: 100%
      z-index: 10
      border-radius: 6px
      box-shadow: 0px 6px 35px rgba(0, 0, 0, 0.36)
      padding: 10px 25px
      max-width: 292px

      &.is-active
        display: block

      &::before
        content: ''
        display: block
        background-color: #fff
        width: 10px
        height: 10px
        position: absolute
        top: 0
        left: 20%
        transform: translate(0,-50%) rotate(45deg)

    +mq-up(xxl)
      display: flex
      color: #3E3E3E
      font-size: 14px
      gap: 40px
      justify-content: flex-start

  &__item
    margin: 5px 0
    +mq-up(xxl)
      margin: 0

  &__link
    display: flex
    align-items: center
    color: currentColor
    text-decoration: none
    line-height: 1.8
    &:hover
      text-decoration: underline
    +mq-up(xxl)
      height: 53px
      align-items: center
      justify-content: center
      text-align: center

  &__more
    +mq-up(xxl)
      display: flex
      align-items: center
      justify-content: center
      position: relative
      z-index: 10
    .icon
      display: none
      max-width: 18px
      cursor: pointer
      fill: #C4C4C4
      +mq-up(xxl)
        display: block

  &__drop
    +mq-up(xxl)
      display: none
      position: absolute
      background-color: #fff
      left: 50%
      top: 100%
      transform: translateX(-50%)
      width: 292px
      border-radius: 6px
      box-shadow: 0px 6px 35px rgba(0, 0, 0, 0.36)
      padding: 10px 0

      &.is-active
        display: block

      &::before
        content: ''
        display: block
        background-color: #fff
        width: 10px
        height: 10px
        position: absolute
        top: 0
        left: 50%
        transform: translate(-50%,-50%) rotate(45deg)

    +mq-down(xxl)
      right: 0
      top: 100%

    #{$self}__link
      +mq-up(xxl)
        padding: 0 25px
        justify-content: flex-start
        text-align: left

  &__button
    display: flex
    flex: 1
    +mq-up(xxl)
      display: none
