.currency
  background: #FFFFFF
  border: 9px solid #F3F3F3
  border-radius: 6px

  &__header
    display: flex

  &__tab
    flex: 1
    display: flex
    align-items: center
    justify-content: center
    text-align: center
    height: 37px
    text-transform: uppercase
    font-size: 13px
    font-weight: 600
    color: #818288
    background-color: #fff
    border-radius: 0 0 6px 6px
    cursor: pointer
    transition: all .4s

    &:hover
      background-color: rgba(#f3f3f3,0.5)

    &:first-child
      border-radius: 0 0 6px 0

    &:last-child
      border-radius: 0 0 0 6px

    &.is-active
      color: $blue
      background-color: #f3f3f3

  &__body
    padding: 0 25px

  &__item
    display: flex
    justify-content: space-between
    gap: 20px
    padding: 15px 0
    &:not(:last-child)
      border-bottom: 1px solid #efefef

  &__token
    .name
      font-size: 15px
      color: #000
      font-weight: 500
    .shortName
      font-size: 13px
      color: #787878
      text-transform: uppercase

  &__price
    text-align: right
    .value
      font-size: 15px
      font-weight: 500
      color: #000
      margin-bottom: 2px
    .delta
      display: flex
      gap: 4px
      align-items: center
      font-size: 10px

      .delta-icon
        display: flex
        align-items: center
        justify-content: center
        width: 11px
        height: 11px
        flex: 0 0 11px
        color: #fff
        border-radius: 3px

        .icon
          width: 7px
          height: 7px

      &--up
        color: #56900C
        .delta-icon
          background-color: #56900c

      &--down
        color: $w-orange
        .delta-icon
          background-color: $w-orange

  &__more
    font-size: 15px
    font-weight: 500
    border-top: 2px solid #f3f3f3
    a
      display: flex
      align-items: center
      justify-content: center
      text-align: center
      height: 46px
