// Font
$font-family-content: "PT Serif", serif;
$font-family-base: "Rubik", sans-serif;
$enable-smooth-scroll: false;

// Grid breakpoints
$grid-breakpoints: (
	xs: 0,
	sm: 576px,
	md: 768px,
	lg: 992px,
	xl: 1200px,
	xxl: 1300px
);

// Grid containers
$container-max-widths: (
	sm: 540px,
	md: 720px,
	lg: 960px,
	xl: 1170px,
	xxl: 1270px
);

// colors
$white: #fff;
$black: #0C2126;
$w-orange: #F94C02;
$p-orange: #F07B04; 
$vibrant: #FCD24C;
$blue: #395DDB
