.tgBox
  display: flex
  flex-direction: column
  gap: 16px
  background: #FFFFFF
  box-shadow: 0px 0px 36px rgba(0, 0, 0, 0.13)
  border-radius: 6px
  padding: 16px 18px

  .tgStatus &
    position: absolute
    width: 295px
    top: 90%
    right: 0
    z-index: 100
    opacity: 0
    pointer-events: none
    transition: all .4s
    &.is-active
      opacity: 1
      pointer-events: all

    &::before
      content: ''
      display: block
      width: 10px
      height: 10px
      transform: rotate(45deg)
      background-color: #fff
      position: absolute
      right: 12px
      top: -5px

  &__row
    display: flex
    gap: 16px

  &__icon
    flex: 0 0 60px

  &__content
    flex: 1
    font-size: 13px
    line-height: 1.07

  &__title
    font-size: 14px
    font-weight: 500
    margin-bottom: 5px
    color: #222

  &__text
    color: #7E7E7E

  &__button
    background-color: #0C2126
    color: $white
    &:hover
      background-color: #3E3E3E
