@use "sass:math";

.icon {
    display: inline-block;
    width: 1em;
    height: 1em;
    fill: currentColor;
}

.icon-arrow-lb {
    width: math.div(7, 7) * 1em;
}
.icon-arrow-left {
    width: math.div(9, 16) * 1em;
}
.icon-arrow-right {
    width: math.div(9, 16) * 1em;
}
.icon-arrow-rt {
    width: math.div(7, 7) * 1em;
