.newsFeed
  background: #F3F3F3
  border-radius: 6px
  padding: 0 25px 34px

  &__header
    display: flex
    align-items: center
    justify-content: space-between
    height: 57px
    border-bottom: 1px solid #e7e7e7
    margin-bottom: 24px

  &__title
    font-size: 16px
    font-weight: 500

  &__all
    font-size: 13px
    font-weight: 500

  &__main
    display: grid
    grid-template-columns: 1fr
    gap: 20px
    +mq-up(md)
      grid-template-columns: 324px 1fr

  &__list
    flex: 1
    display: flex
    flex-direction: column
    overflow: auto
    height: 258px
    gap: 12px
    padding-right: 28px

    .simplebar-content
      display: flex
      flex-direction: column
      gap: 12px

  &__item
    display: flex
    gap: 17px
    width: 100%

    .newsFeed__first &
      display: flex
      flex-direction: column
      width: 100%
      gap: 0

      +mq-up(lg)
        flex: 0 0 324px

  &__image
    width: 139px
    height: 78px
    border-radius: 4px
    flex: 0 0 139px
    overflow: hidden

    .newsFeed__first &
      width: 100%
      flex: 0 0 182px
      height: 182px
      overflow: hidden
      border-radius: 4px
      margin-bottom: 13px

      img
        width: 100%
        height: 100%
        object-fit: cover

  &__name
    color: #2D2D2D
    font-size: 15px
    line-height: 1.1
    font-weight: 500
    margin-bottom: 6px

    a
      color: currentColor
      text-decoration: none
      &:hover
        text-decoration: underline

    .newsFeed__first
      font-size: 16px
      font-weight: 600
      line-height: 1.44
      margin-bottom: 8px

  &__info
    display: flex
    align-items: center
    color: #A6A6A6
    gap: 14px
    font-size: 13px

    .newsFeed__first &
      &::before
        content: ''
        display: inline-block
        width: 4px
        height: 4px
        border-radius: 50%
        background-color: currentColor

  &__comment
    display: flex
    align-items: center
    gap: 4px
    order: 1

    .newsFeed__first &
      order: -1

    &.hot
      color: $w-orange
