.module
    margin-bottom: 20px

    &--big-space
        margin-bottom: 56px

    &--sidebar
        margin-bottom: 20px

    &__title
        font-size: 24px
        margin-bottom: 20px
        font-weight: 600
        margin-top: 0
        +mq-up(lg)
            font-size: 32px

    &__more
        &-link
            display: flex
            align-items: center
            justify-content: center
            text-align: center
            text-decoration: none
            background: #E0EEFA
            border-radius: 6px
            font-size: 14px
            font-weight: 600
            padding: 6px 20px
            transition: all .4s
            &:hover
                background: darken(#E0EEFA, 5)

            .icon
                margin-left: 9px
                flex: 0 0 17px
