.profile-menu
  $self: &
  @extend .module
  background: #222222
  border-radius: 6px
  overflow: hidden
  +mq-up(lg)
    margin-left: auto
    width: 295px
    flex: 0 0 295px

  &__item
    &:nth-child(odd)
      #{$self}__icon
        background-color: #323232
      #{$self}__text
        border-top: 1px solid #323232
        border-bottom: 1px solid #323232

    &:nth-child(even)
      #{$self}__icon
        background-color: #272727

    &:last-child
      #{$self}__text
        color: $w-orange

  &__link
    display: flex
    height: 50px
    align-items: center
    text-decoration: none
    text-transform: uppercase
    color: #fff
    font-size: 11px
    font-weight: 600

    &:hover, &.is-active
      color: #E8A33C

      #{$self}__icon
        color: inherit

  &__icon
    display: flex
    align-items: center
    justify-content: center
    width: 50px
    height: 50px
    flex: 0 0 50px
    color: #ADADAD
    position: relative

    span
      display: flex
      align-items: center
      justify-content: center
      position: absolute
      border-radius: 50px
      background-color: $w-orange
      width: 15px
      height: 15px
      color: #fff
      font-size: 9px
      right: 8px
      top: 9px

    .icon
      width: 18px
      height: 18px
      flex: 0 0 18px

  &__text
    display: flex
    align-items: center
    height: 50px
    padding: 0 15px
    flex: 1

    sup
      display: inline-block
      font-weight: 400
      font-size: 12px
      margin-left: 3px
      margin-top: -10px
      color: #B4B4B4
