.subscribe
  background-color: #f3f3f3
  padding: 15px 0

  &.subscribeBox
    background: transparent url('../images/lines.svg') repeat left top
    padding: 10px
    border-radius: 6px

    > *
      height: 100%

  &__row
    display: flex
    flex-direction: column
    gap: 30px
    +mq-up(lg)
      flex-direction: row
      align-items: center

    .subscribeBox &
      background-color: #fff
      flex-direction: column
      padding: 10px 25px 25px
      height: 100%

  &__title
    font-size: 22px
    line-height: 1.27
    font-weight: 500

    .subscribeBox &
      font-size: 18px

  &__text
    color: #7E7E7E
    font-size: 15px
    line-height: 1.4

    .subscribeBox &
      font-size: 14px

  &__content
    display: flex
    gap: 18px
    align-items: start
    flex-wrap: nowrap

    .subscribeBox &
      width: 100%
      flex-direction: column

  &__icon
    margin-top: -30px
    flex: 0 0 67px

  &__field
    display: flex
    align-items: center
    height: 54px
    gap: 10px
    background-color: #fff
    border-radius: 8px
    position: relative

    .input-form
      background-color: transparent
      height: 100%

    .subscribeBox &
      background-color: #F2F2F2

    +mq-up(sm)
      flex: 0 0 297px

  &__mail-icon
    flex: 0 0 16px

  &__input
    border: none
    outline: none
    font-weight: 500
    font-family: inherit
    font-size: 13px
    height: 17px
    width: 100%
    background-color: transparent
    +mq-down(sm)
      width: 100%

  &__label
    color: #5C6162
    font-size: 12px

  &__form
    display: grid
    grid-template-columns: 1fr
    gap: 20px
    +mq-up(md)
      grid-template-columns: 297px 225px
    +mq-up(lg)
      margin-left: auto

    .subscribeBox &
      width: 100%
      +mq-up(lg)
        gap: 0
        grid-template-columns: 1fr 225px

  &__submit
    display: flex

    +mq-up(md)
      min-width: 225px
      flex: 0 0 225px
      height: auto
