.login
  &__social
    &-text
      color: #565656
      font-size: 12px
      margin-bottom: 5px
    &-icons
      display: flex
      flex-wrap: wrap
      justify-content: center
      gap: 8px
      margin-bottom: 23px
    &-link
      display: flex
      align-items: center
      justify-content: center
      color: #fff
      text-decoration: none
      width: 40px
      height: 40px
      flex: 0 0 40px
      border-radius: 6px
      .icon
        width: 20px
        height: 20px

  &__form
    display: flex
    flex-direction: column
    gap: 15px
    text-align: center

    .lost-pass
      font-size: 13px
      color: #2B2D33

  &__content
    &:not(:last-child)
      margin-bottom: 20px

  .btn
    width: 100%
