.post-list
  border-radius: 6px
  background-color: #F3F3F3
  padding: 0 24px 25px
  &__title
    display: flex
    align-items: center
    height: 58px
    border-bottom: 1px solid #E7E7E7
    font-weight: 500
    font-size: 16px
    margin-bottom: 20px
  &__list
    display: flex
    flex-direction: column
    gap: 17px
  &__item
    display: flex
    align-items: center
    gap: 8px
    font-size: 15px
    &::before
      content: ''
      display: block
      width: 4px
      height: 4px
      flex: 0 0 4px
      background-color: #C8C8C8
      border-radius: 50%
  &__date
    color: #A6A6A6
    font-size: 13px
    font-weight: 300
    order: -1
  &__link
    color: currentColor
    text-decoration: none
    &:hover
      text-decoration: underline
      color: $w-orange
  &__comment
    display: flex
    align-items: center
    gap: 4px
    color: #D7D7D7
    font-size: 13px
    &.hot
      color: $w-orange
