.box-404
  text-align: center
  margin-bottom: 120px
  &__image
    display: flex
    align-items: center
    justify-content: center
    height: 422px
    background: url('../images/404.webp') no-repeat center
    img
      width: 396px
      height: 396px
      max-width: 100%
      object-fit: contain
  &__content
    margin: 0 auto
    max-width: 608px
    display: flex
    flex-direction: column
    align-items: center
  &__title
    font-size: 18px
    font-weight: 500
    text-align: center
    margin: 0 auto
  &__text
    font-size: 15px
    color: #575757
    line-height: 1.6
    margin-bottom: 34px
    text-align: center
  .btn
    min-width: 225px
