.header
  margin-bottom: 40px
  &__top
    background-color: #222
  &__row
    display: flex
    align-items: stretch
    gap: 10px 30px
    position: relative
    +mq-down(lg)
      display: none
  &__bottom
    border-bottom: 1px solid #EBEBEB
  &__mobile
    display: none
    +mq-down(lg)
      display: flex
      align-items: center
      gap: 10px 30px
      position: relative

    &-drop
      position: absolute
      top: calc(100% + 10px)
      width: 250px
      left: 0
      background-color: #222
      z-index: 100
      padding: 20px
      border-radius: 6px
      opacity: 0
      pointer-events: none
      transition: .4s

      &.is-active
        opacity: 1
        pointer-events: all
