.comment
  margin-bottom: 24px
  .profile-comments &
    margin-bottom: 10px
  &.reply
    display: none
    &.is-active
      display: block
    &:nth-last-child(2)
      margin-bottom: 20px
  &__header
    position: relative
    display: flex
    align-items: center
    gap: 15px
    color: #999999
    padding-bottom: 12px
    border-bottom: 1px solid #E6E6E6
    margin-bottom: 16px
    .profile-comments &
      padding: 12px 30px
    &::before
      content: ''
      display: block
      position: absolute
      width: 10px
      height: 10px
      background-color: #fff
      transform: rotate(45deg)
      left: 44px
      bottom: -5px
      border-bottom: 1px solid #e6e6e6
      border-right: 1px solid #e6e6e6

  &__avatar
    flex: 0 0 35px
    width: 35px
    height: 35px
    border-radius: 50%
    position: relative

    img
      width: 100%
      height: 100%
      object-fit: cover
      border-radius: inherit

    &::before
      content: ''
      display: block
      width: 11px
      height: 11px
      position: absolute
      top: 0
      right: 0
      background-color: #858585
      border: 2px solid #fff
      border-radius: 50%

    &.online
      &::before
        background-color: #56900C

  &__author
    font-size: 13px
    color: #000
    font-weight: 500

  &__bull
    width: 4px
    height: 4px
    flex: 0 0 4px
    border-radius: 50%
    background-color: currentColor

  &__time
    font-size: 12px

  &__liked
    display: flex
    align-items: center
    gap: 10px
    font-size: 13px
    font-weight: 500

    .profilepage &
      pointer-events: none

    .icon
      cursor: pointer
      &-like
        &:hover, &.is-active
          fill: #56900C
      &-dislike
        &:hover, &.is-active
          fill: $w-orange

    &-value
      color: #B9B9B9
      &.positive
        color: #56900C
      &.negative
        color: $w-orange

  &__post
    margin-left: auto
    font-weight: 400
    text-transform: uppercase
    font-size: 10px
    height: 30px

  &__body
    font-size: 14px
    line-height: 1.57
    margin-bottom: 11px
    .profile-comments &
      padding: 0 30px

  &__footer
    display: flex
    flex-wrap: wrap
    gap: 20px
    justify-content: space-between
    .profile-comments &
      padding: 0 30px

  &__reply
    display: inline-flex
    align-items: center
    color: $w-orange
    gap: 7px
    font-size: 13px
    cursor: pointer

    .icon
      width: 11px
      height: 10px
      flex: 0 0 11px

  &__action
    display: flex
    align-items: center
    gap: 43px

    a
      display: flex
      align-items: center
      text-decoration: none
      font-size: 13px
      gap: 5px

      &.edit
        color: #56900C

      &.remove
        color: $w-orange

      .icon
        width: 12px
        height: 12px
        flex: 0 0 12px
