.search

  .header &
    display: flex
    align-items: center
    color: #fff
    margin-left: auto

    &__button
      display: flex
      align-items: center
      justify-content: center
      width: 18px
      height: 18px
      cursor: pointer

    &__form
      width: 100%
      position: absolute
      z-index: 100
      top: 0
      right: 0
      bottom: 0
      left: 0
      background-color: #222
      display: flex
      align-items: center
      gap: 15px
      padding: 0
      border-radius: 6px
      opacity: 0
      pointer-events: none
      transition: all .4s

      &.is-active
        opacity: 1
        pointer-events: all

      [type="search"]
        background-color: transparent
        border: none
        color: #fff
        font-family: inherit
        display: block
        height: 22px
        width: calc(100% - 55px)
        outline: none

      [type="submit"]
        display: block
        background-color: transparent
        border: none
        color: #B1B1B1
        width: 18px
        height: 18px
        flex: 0 0 18px
        padding: 0
        margin: 0
        cursor: pointer
        margin-left: auto
