.cardFill
  position: relative
  border-radius: 6px
  overflow: hidden

  &::before
    content: ''
    display: block
    width: 100%
    height: 100%
    position: absolute
    left: 0
    top: 0
    z-index: 2
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 67.2%, #000000 100%)

  &:hover
    &::before
      background: linear-gradient(180deg, rgba(0, 0, 0, .2) 67.2%, #000000 100%)

  &.themeOfTheDay,
  &.editorChoice
    height: 333px

  &.editorChoice
    flex: 0 0 295px

  &__link
    display: block
    height: 100%
    padding: 25px 25px 20px
    color: #fff
    text-decoration: none

  &__image
    position: absolute
    width: 100%
    height: 100%
    object-fit: cover
    z-index: 1
    left: 0
    top: 0

  &__frame
    display: flex
    flex-direction: column
    justify-content: space-between
    height: 100%
    position: relative
    z-index: 3

  &__date
    color: #C8C8C8
    font-size: 12px
    line-height: 14px
    margin-bottom: 9px

  &__name
    font-size: 22px
    font-weight: 600
    line-height: 26px

  &__top
    display: flex
    justify-content: space-between
    gap: 30px

  &__tag
    display: flex
    align-items: center
    gap: 6px
    flex: 1

    .themeOfTheDay &
      color: $vibrant

  &__tagText
    text-transform: uppercase
    font-size: 10px
    line-height: 12px
    font-weight: 600

  &__tagIcon
    flex: 0 0 22px

  &__comment
    display: flex
    align-items: center
    gap: 5px
    font-size: 13px
    margin-left: auto

    &.hot
      color: $w-orange
      font-weight: 700

  &__commentIcon
    flex: 0 0 16px
