.input-form
  display: flex
  align-items: center
  width: 100%
  height: 48px
  background: #F2F2F2
  border-radius: 6px
  position: relative

  &.with-icon
    input
      padding-left: 38px
    label
      left: 38px

  label
    position: absolute
    left: 16px
    font-size: 12px
    color: #5C6162
    pointer-events: none
    transition: all .4s

  &__result
    position: absolute
    z-index: 10
    top: calc(100% + 5px)
    font-size: 12px
    width: 100%
    .profile-form &
      top: 50%
      transform: translateY(-50%)
      left: calc(100% + 20px)
    .subscribe &
      bottom: calc(100% + 25px)
      top: auto
    > *
      align-items: center
      gap: 5px
      display: none
      &.is-active
        display: flex
      .icon
        width: 18px
        height: 18px
        flex: 0 0 18px

    .valid
      color: #56900C
    .invalid
      color: $w-orange

  &__icon
    position: absolute
    left: 10px
    top: 50%
    transform: translateY(-50%)

  input
    border: none
    flex: 1
    width: 100%
    outline: none
    height: 100%
    padding: 0 16px
    background-color: transparent
    font-size: 13px
    font-weight: 500
    border-radius: inherit
    padding-right: 40px

    &:invalid, &.error
      box-shadow: 0px 4px 15px rgba(249, 61, 2, 0.3)
      border: 1px solid $w-orange
      color: $w-orange

    &:focus, &[data-empty="false"]
      padding-top: 9px
      & + label
        top: 5px

.input-checkbox
  display: inline-flex
  align-items: center
  cursor: pointer
  font-size: 13px
  gap: 11px
  user-select: none

  &__input
    display: flex
    align-items: center
    justify-content: center
    width: 28px
    height: 28px
    flex: 0 0 28px
    border-radius: 6px
    background-color: #F2F2F2
    position: relative

    .icon
      display: block
      width: 14px
      height: 14px
      position: absolute
      opacity: 0
      pointer-events: none

  input
    width: 1px
    height: 1px
    opacity: 0
    pointer-events: none

    &:checked ~ .icon
      opacity: 1
