.mainmenu
  .footer &
    margin: 0 auto
    +mq-up(lg)
      margin: 0

  .header__mobile &
    margin-bottom: 20px

  &__list
    display: flex
    gap: 25px
    height: 100%
    +mq-up(lg)
      gap: 45px

    .header &
      +mq-down(lg)
        flex-direction: column
        gap: 5px

  &__item
    color: #9C9C9C
    font-size: 15px
    font-weight: 500
    &.is-active
      color: #fff
      position: relative

      &::after
        display: block
        content: ''
        position: absolute
        left: 0
        bottom: 0
        right: 0
        height: 3px
        background-color: $w-orange

    a
      display: flex
      align-items: center
      justify-content: center
      text-decoration: none
      height: 100%
      padding: 15px 0
      color: currentColor

      .header__mobile &
        padding: 10px 0
