.youtube
  $self: &
  padding: 25px
  background: #FFFFFF
  border: 1px solid #F1F1F1
  border-radius: 6px
  +mq-up(lg)
    padding: 37px

  &--widget
    background: #F3F3F3
    border-radius: 6px
    padding: 0

    #{$self}__more
      border-top: 2px solid #E6E6E6
      a
        display: flex
        align-items: center
        justify-content: center
        text-align: center
        height: 47px
        font-size: 15px
        font-weight: 500

    #{$self}__el
      > a
        display: flex
        gap: 10px
        color: #454545
        font-size: 14px
        line-height: 1.29
        text-decoration: none
        &:hover
          text-decoration: underline

  &__header
    display: flex
    flex-wrap: wrap
    align-items: center
    margin-bottom: 30px
    gap: 15px

    #{$self}--widget &
      padding: 18px 22px
      margin-bottom: 0

  &__list
    display: flex
    flex-direction: column
    padding: 0 25px 25px
    gap: 20px

  &__headerIcon
    width: 30px
    height: 30px
    flex: 0 0 30px
    +mq-up(md)
      width: 51px
      height: 51px
      flex: 0 0 51px

    #{$self}--widget &
      flex: 0 0 29px
      width: 29px
      height: 29px

  &__title
    @extend .module__title
    margin: 0

    #{$self}--widget &
      font-size: 18px
      font-weight: 600

  &__navigation
    display: flex
    align-items: center
    margin-left: auto
    gap: 9px

  &__nav
    display: flex
    align-items: center
    justify-content: center
    width: 40px
    height: 40px
    flex: 0 0 40px
    border-radius: 50%
    border: 1px solid #DCDCDC
    cursor: pointer
    user-select: none

    &.swiper-button-disabled
      color: #879599

  &__swiper
    min-width: 0
    overflow: hidden

  &__item
    height: 210px
    position: relative
    color: #fff

    > a
      display: block
      height: 100%
      overflow: hidden
      border-radius: 6px
      position: relative
      text-decoration: none
      color: currentColor

      &::before
        content: ''
        display: block
        position: absolute
        left: 0
        top: 0
        right: 0
        bottom: 0
        background: linear-gradient(180deg, rgba(17, 21, 24, 0) 0%, rgba(17, 21, 24, 0.84) 84.29%)
        z-index: 1

    .play
      display: flex
      align-items: center
      gap: 11px
      text-transform: uppercase
      position: absolute
      left: 14px
      bottom: 13px
      z-index: 2

      &__icon
        display: flex
        align-items: center
        justify-content: center
        width: 48px
        height: 48px
        flex: 0 0 48px
        border-radius: 50%
        background-color: $w-orange

      &__text
        font-size: 11px
        line-height: 13px
        text-transform: uppercase
        font-weight: 500

  &__preview
    .yotube:not(.youtube--widget) &
      width: 100%
      height: 100%
      object-fit: cover
    #{$self}--widget &
      width: 88px
      height: 55px
      border-radius: 3px
      flex: 0 0 88px

  &__channel
    &:hover
      border-color: #494949
      color: #525252
      background-color: #fff
