.post
  margin-bottom: 73px
  &__header
    border-left: 5px solid $vibrant
    padding-left: 20px
    margin-bottom: 20px

  &__title, h1
    font-size: 30px
    font-weight: 600
    margin-bottom: 10px
    +mq-up(lg)
      font-size: 35px

  &__subtitle, h2
    font-size: 26px
    font-weight: 600
    font-family: $font-family-base
    padding-bottom: 20px
    position: relative
    line-height: 1
    color: #161616
    @extend .module
    &::before
      content: ""
      display: block
      position: absolute
      width: 60px
      height: 5px
      background-color: $vibrant
      left: 0
      bottom: 0
    +mq-up(lg)
      font-size: 32px

  &__content
    font-size: 17px
    line-height: 1.71
    color: #000
    font-family: $font-family-content
    p:not(:last-child)
      margin-bottom: 26px
    blockquote
      color: #232323
      border-left: 11px solid $vibrant
      position: relative
      padding: 25px 0 5px 25px
      font-style: italic
      margin: 30px 0
      strong
        font-style: normal
      &::before
        content: ''
        display: block
        width: 54px
        height: 36px
        position: absolute
        left: 25px
        top: 0
        transform: translateY(-50%)
        background: url('../images/post/blockquote.svg') no-repeat center

    figure
      display: flex
      flex-direction: column
      align-items: center
      margin-bottom: 26px
      img
        margin-bottom: 14px
      figcaption
        color: #696969
        width: 335px
        margin: 0 auto
        text-align: center
        font-size: 14px
        line-height: 18px
        font-family: $font-family-base
        a
          color: $blue

    ol,ul
      &:not([class])
        margin-bottom: 26px

    ul:not([class])
      li
        display: flex
        align-items: center
        gap: 10px
        &::before
          content: ''
          display: block
          width: 14px
          height: 14px
          flex: 0 0 14px
          background: url('../images/post/listMark2.svg') no-repeat center

    ol
      counter-reset: ol
      li
        counter-increment: ol
        display: flex
        align-items: center
        gap: 10px
        line-height: 1.71
        &::before
          display: flex
          align-items: center
          justify-content: center
          content: counter(ol)
          width: 24px
          height: 24px
          flex: 0 0 24px
          background-color: $vibrant
          font-family: $font-family-base
          border-radius: 50%
          font-size: 12px
          font-weight: 600

    a:not([class])
      color: $w-orange

  &__article
    color: #0C2126
    font-family: $font-family-base
    padding: 17px 20px 20px
    background-color: #F4F4F2
    border-left: 5px solid currentColor

    &-title
      font-size: 18px
      font-weight: 500
      line-height: 1.44
      margin-bottom: 12px

    &-list
      font-size: 15px

    &-item
      display: flex
      gap: 14px
      align-items: center
      line-height: 1.87

      &::before
        content: ''
        display: block
        width: 12px
        height: 12px
        flex: 0 0 12px
        background: url('../images/post/listMark.svg') no-repeat center

    &-link
      color: $blue

  .module
    margin-bottom: 26px
