.post-comment
  margin-bottom: 73px
  &__title
    @extend .module__title

    sup
      font-size: 16px
      color: #787878
      font-weight: 400

  &__auth
    @extend .module
    background: #FFFFFF
    border: 1px dashed #D0D0D0
    border-radius: 6px
    padding: 16px 25px 25px
    display: flex
    flex-direction: column
    align-items: center
    gap: 10px

  &__noauth
    @extend .module
    background: #FFFFFF
    border: 1px dashed $vibrant
    border-radius: 6px
    display: flex
    flex-direction: column
    align-items: center
    gap: 20px
    padding: 25px

  &__pizza
    width: 184px
    height: 169px
    flex: 0 0 169px
    object-fit: contain

  &__description
    font-size: 15px
    color: #575757

  &__row
    display: flex
    gap: 15px

  &__btn
    height: 46px

  &__reply
    &.show
      margin-left: 105px

  &__hider
    display: inline-flex
    align-items: center
    gap: 8px
    font-size: 13px
    color: #777777
    margin-bottom: 34px
    cursor: pointer

    &::before
      content: ''
      display: block
      width: 7px
      height: 7px
      background: #56900C
      transform: rotate(45deg)
      background: linear-gradient(-45deg, #56900C 50%,#fff 50%, #fff 100%)
      margin-top: -4px

    &.show
      &::before
        transform: rotate(227deg)
        margin: 0

  &__write
    margin-top: 44px
    &-title
      font-size: 18px
      margin-bottom: 11px
    &-box
      background-color: #F2F2F2
      border-radius: 3px
      padding: 8px 15px
      display: flex
      justify-content: stretch

      .textarea,textarea
        display: block
        flex: 1
        border: none
        outline: none
        resize: none
        background-color: transparent
        min-height: 55px
        font-size: 12px
        font-family: inherit

      textarea::placeholder
        content: attr(data-text)
        color: #7D7D7D

      [contentEditable=true]:empty:not(:focus):before
        content: attr(data-text)
        color: #7D7D7D

    &-actions
      display: flex
      flex-direction: column
      justify-content: flex-end
      gap: 12px
      color: #2D2D2D
      flex: 0 0 18px

      button
        padding: 0
        margin: 0
        border: none
        outline: none
        cursor: pointer
        width: 18px
        height: 20px
        flex: 0 0 20px

        .icon
          width: 18px
          height: 18px
          vertical-align: middle
