.pizzaBox
  background: #FFFFFF url('../images/pizzaBox/pizza.webp') no-repeat right center
  border: 9px solid #FFF3CE
  box-sizing: border-box
  border-radius: 6px
  padding: 19px 16px

  &__info
    display: flex
    align-items: center
    justify-content: center
    width: 20px
    height: 20px
    flex: 0 0 20px
    border: 1px solid #E1E1E1
    border-radius: 50%
    color: #6B6B6B
    cursor: pointer

  &__header
    display: flex
    align-items: center
    gap: 10px
    max-width: 150px

  &__title
    font-weight: 600
    line-height: 1.17
    font-size: 18px

  &__btc
    color: #585858
    font-size: 14px
    text-transform: uppercase
    line-height: 21px
    font-weight: 600
    margin-bottom: 7px

  &__text
    color: #6A6A6A
    font-size: 13px
    line-height: 17px
    margin-bottom: 15px
    max-width: 150px

  &__price
    color: $w-orange
    font-weight: 600
    font-size: 18px
    line-height: 21px
    margin-bottom: 9px

  &__counter
    font-size: 12px
    line-height: 14px
    color: #868686
